import Vue from "vue";
import Vuex from "vuex";

import getters from "./getters";
import actions from "./actions";
import mutations from "./mutations";

Vue.use(Vuex);

export const getCleanNode = () => ({
  name: { value: "", error: "" },
  _type: { value: "", error: "" },
  latitude: { value: "", error: "" },
  longitude: { value: "", error: "" }
});

export const getCleanLink = () => ({
  name: { value: "", error: "" },
  fiber_capacity: { value: "", error: "" },
  channel_capacity: { value: "", error: "" },
  length: { value: "", error: "" },
  node_u: { value: "", error: "" },
  node_v: { value: "", error: "" }
});

export default new Vuex.Store({
  state: {
    nodes: [],
    nodeInEdit: getCleanNode(),
    links: [],
    linkInEdit: getCleanLink(),
    editingNodeOrLink: false,
    network: {
      name: "",
      network_year: "",
      should_calculate_distance: false,
      calculation_formula: ""
    }
  },
  getters,
  actions,
  mutations
});
