<template>
  <form>
    <div class="form-row">
      <VInput
        id="node-name"
        v-model="nodeInEdit.name.value"
        :parent-error="nodeInEdit.name.error"
        class="col-md-3 col-sm-6"
        label="Node Name:"
        maxlength="255"
        required
      />
      <VInput
        id="node-type"
        v-model="nodeInEdit._type.value"
        :parent-error="nodeInEdit._type.error"
        label="Node Type:"
        class="col-md-3 col-sm-6"
      />
      <VInput
        id="node-latitude"
        v-model="nodeInEdit.latitude.value"
        :parent-error="nodeInEdit.latitude.error"
        label="Node Latitude:"
        class="col-md-3 col-sm-6"
      />
      <VInput
        id="node-longitude"
        v-model="nodeInEdit.longitude.value"
        :parent-error="nodeInEdit.longitude.error"
        label="Node Longitude:"
        class="col-md-3 col-sm-6"
      />
      <div class="col">
        <button type="submit" class="btn btn-success" @click.prevent="addNode">
          {{ editingNodeOrLink ? "save node" : "add node" }}
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import VInput from "./VInput";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "FormInputNode",
  components: { VInput },
  computed: mapGetters(["nodeInEdit", "editingNodeOrLink"]),
  methods: mapActions(["addNode"])
};
</script>
