import axios from "axios";
import { getCleanNode, getCleanLink } from "./index.js";

const instance = axios.create({
  baseURL: "http://localhost:8000/api/",
  timeout: 1000
});

// const cleanNetowork = {
//   name: "",
//   network_year: "",
//   should_calculate_distance: false,
//   calculation_formula: null
// };

const createNewNodeOrLink = item => {
  let newItem = {};
  for (let field in item) newItem = { ...newItem, [field]: item[field].value };
  return newItem;
};

export default {
  async submitForm({ state, commit }) {
    await instance.post("", {
      ...state.network,
      calculation_formula:
        state.calculation_formula === "" ? null : state.calculation_formula,
      nodes: state.nodes,
      links: state.links
    });
    commit("updateNodeInEdit", getCleanNode());
    commit("updateLinkInEdit", getCleanLink());
    commit("restartState");
  },
  addNode({ state, commit }) {
    const { nodeInEdit } = state;
    if (nodeInEdit.name.value === "") {
      commit("updateNodeInEdit", {
        ...nodeInEdit,
        name: { value: nodeInEdit.name.value, error: "Required field" }
      });
      return;
    }
    const newNode = createNewNodeOrLink(nodeInEdit);
    if (state.editingNodeOrLink) {
      commit("overwriteNode", newNode);
      commit("setEditingNodeOrLink", false);
    } else {
      for (let node of state.nodes)
        if (node.name === nodeInEdit.name.value) {
          commit("updateNodeInEdit", {
            ...nodeInEdit,
            name: {
              value: nodeInEdit.name.value,
              error: "The node name must be unique"
            }
          });
          return;
        }
      commit("addNode", newNode);
    }
    commit("updateNodeInEdit", getCleanNode());
  },
  addLink({ state, commit }) {
    const { linkInEdit } = state;
    if (linkInEdit.name.value === "") {
      commit("updateLinkInEdit", {
        ...linkInEdit,
        name: { value: linkInEdit.value, error: "Required field" }
      });
      return;
    }
    if (linkInEdit.node_u.value === linkInEdit.node_v.value) {
      commit("updateLinkInEdit", {
        ...linkInEdit,
        node_u: {
          value: linkInEdit.node_u.value,
          error: "Nodes can not be the same"
        },
        node_v: {
          value: linkInEdit.node_v.value,
          error: "Nodes can not be the same"
        }
      });
      return;
    }
    const newLink = createNewNodeOrLink(linkInEdit);
    if (state.editingNodeOrLink) {
      commit("overwriteLink", newLink);
      commit("setEditingNodeOrLink", false);
    } else {
      for (let link in state.links) {
        if (link.name == linkInEdit.name.value) {
          commit("updateLinkInEditing", {
            ...linkInEdit,
            name: {
              value: linkInEdit.name.value,
              error: "The node name must be unique"
            }
          });
          return;
        }
      }
      commit("addLink", newLink);
    }
    commit("updateLinkInEdit", getCleanLink());
  },
  editNode({ state, commit }, nodeName) {
    let nodeToEdit = state.nodes.find(node => node.name === nodeName);
    for (let field in nodeToEdit)
      nodeToEdit = {
        ...nodeToEdit,
        [field]: { value: nodeToEdit[field], error: "" }
      };
    commit("updateNodeInEdit", nodeToEdit);
    commit("setEditingNodeOrLink", nodeToEdit.name.value);
  },
  editLink({ state, commit }, linkName) {
    let linkToEdit = state.links.find(link => link.name === linkName);
    for (let field in linkToEdit)
      linkToEdit = {
        ...linkToEdit,
        [field]: { value: linkToEdit[field], error: "" }
      };
    commit("updateLinkInEdit", linkToEdit);
    commit("setEditingNodeOrLink", linkToEdit.name.value);
  }
};
