import Vue from "vue";
import VueRouter from "vue-router";

import HomePage from "../views/HomePage";
import NetworkRegistrationPage from "../views/NetworkRegistrationPage";
import NetworkFormPage from "../views/NetworkFormPage";
import NodeListPage from "../views/NodeListPage";
import LinkListPage from "../views/LinkListPage";

Vue.use(VueRouter);

export default new VueRouter({
  routes: [
    { path: "/", component: HomePage },
    {
      path: "/registration",
      component: NetworkRegistrationPage,
      children: [
        { path: "", component: NetworkFormPage },
        { path: "/nodes", component: NodeListPage },
        { path: "/links", component: LinkListPage }
      ]
    }
  ],
  linkActiveClass: "active",
  linkExactActiveClass: "active"
});
