<template>
  <div class="form-group">
    <label v-if="label" :for="$attrs.id">{{ label }}</label>
    <input
      v-model="display"
      v-bind="$attrs"
      :type="$attrs.type === 'number' ? 'tel' : $attrs.type"
      :class="{ 'is-invalid': parentError || error }"
      class="form-control"
      @blur="onBlur"
    />
    <div v-if="parentError || error" class="invalid-feedback">
      {{ parentError ? parentError : error }}
    </div>
  </div>
</template>

<script>
const requiredField = "Required field";

export default {
  name: "VInput",
  inheritAttrs: false,
  props: {
    label: { type: String, default: "" },
    value: { type: String, default: "" },
    parentError: { type: String, default: "" }
  },
  data() {
    return { display: "", error: "" };
  },
  watch: {
    display(val, oldVal) {
      if (this.$attrs.type === "number") {
        this.display = !Number.isNaN(Number(val)) ? val : oldVal;
      } else {
        const maxlen = this.$attrs.maxlength;
        if (maxlen !== undefined)
          this.display = this.display.substring(0, maxlen);
      }
      if (this.value !== this.display) this.$emit("input", this.display);
    },
    value() {
      this.display = this.value;
    }
  },
  methods: {
    onBlur() {
      this.error =
        this.$attrs.required != undefined && this.value == ""
          ? requiredField
          : "";
      if (
        this.$attrs.type === "number" &&
        this.display[this.display.length - 1] === "."
      )
        this.display = this.display + "0";
    }
  }
};
</script>

<style scoped>
input:focus,
input.is-invalid:focus,
input.is-valid:focus {
  box-shadow: none;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}
</style>
