<template>
  <div class="form-group">
    <label v-if="label" :for="$attrs.id">{{ label }}</label>
    <select
      :id="$attrs.id"
      :value="value"
      :class="{ 'is-invalid': parentError || error }"
      v-bind="$attrs"
      class="form-control custom-select"
      @change="$emit('change', $event.target.value)"
      @blur="onBlur"
    >
      <option disabled value="">Choose a option</option>
      <option
        v-for="(option, index) in options"
        :key="index"
        :value="option[keys.value]"
      >
        {{ option[keys.text] }}
      </option>
    </select>
    <div v-if="parentError || error" class="invalid-feedback">
      {{ parentError ? parentError : error }}
    </div>
  </div>
</template>

<script>
const requiredField = "Required field";

export default {
  name: "VSelect",
  inheritAttrs: false,
  model: { prop: "value", event: "change" },
  props: {
    value: { type: String, default: "" },
    label: { type: String, default: "" },
    options: { type: Array, default: () => [] },
    keys: { type: Object, default: () => ({ value: "id", text: "name" }) },
    parentError: { type: String, default: "" }
  },
  data() {
    return { error: "" };
  },
  methods: {
    onBlur() {
      if (this.$attrs.required != undefined && this.value == "") {
        this.error = requiredField;
        return;
      }
      this.error = "";
    }
  }
};
</script>

