export default {
  addNode(state, node) {
    state.nodes.push(node);
  },
  addLink(state, link) {
    state.links.push(link);
  },
  updateNetwork(state, network) {
    state.network = network;
  },
  updateNodeInEdit(state, node) {
    state.nodeInEdit = node;
  },
  updateLinkInEdit(state, link) {
    state.linkInEdit = link;
  },
  removeNode(state, nodeName) {
    state.nodes = state.nodes.filter(node => node.name !== nodeName);
    state.links = state.links.filter(
      link => link.node_u !== nodeName && link.node_v !== nodeName
    );
  },
  removeLink(state, linkName) {
    state.links = state.links.filter(link => link.name !== linkName);
  },
  setEditingNodeOrLink(state, nodeOrLink) {
    state.editingNodeOrLink = nodeOrLink;
  },
  overwriteNode(state, newNode) {
    state.nodes = state.nodes.map(node =>
      node.name === state.editingNodeOrLink ? { ...newNode } : node
    );
    state.links.forEach(link => {
      if (link.node_u === state.editingNodeOrLink) link.node_u = newNode.name;
      if (link.node_v === state.editingNodeOrLink) link.node_v = newNode.name;
    });
  },
  overwriteLink(state, newLink) {
    state.links = state.links.map(link =>
      link.name === state.editingNodeOrLink ? { ...newLink } : link
    );
  },
  restartState(state) {
    state.network = {
      name: "",
      network_year: "",
      should_calculate_distance: false,
      calculation_formula: ""
    };
    state.links = [];
    state.nodes = [];
  }
};
