<template>
  <form>
    <div class="form-row">
      <VInput
        id="link-name"
        v-model="linkInEdit.name.value"
        :parent-error="linkInEdit.name.error"
        label="Link Name:"
        class="col-md-4 col-sm-6"
        maxlength="255"
        required
      />
      <VInput
        id="fiber-capacity"
        v-model="linkInEdit.fiber_capacity.value"
        :parent-error="linkInEdit.fiber_capacity.error"
        class="col-md-4 col-sm-6"
        label="Number of Fibers:"
        type="number"
      />
      <VInput
        id="channel-capacity"
        v-model="linkInEdit.channel_capacity.value"
        :parent-error="linkInEdit.channel_capacity.error"
        class="col-md-4 col-sm-6"
        label="Number of Channels:"
        type="number"
      />
      <VInput
        id="link-length"
        v-model="linkInEdit.length.value"
        :parent-error="linkInEdit.length.error"
        :required="shouldCalculateDistance"
        class="col-md-4 col-sm-6"
        label="Link Length:"
        type="number"
      />
      <VSelect
        id="node-u"
        v-model="linkInEdit.node_u.value"
        :parent-error="linkInEdit.node_u.error"
        :options="nodes"
        :keys="{ value: 'name', text: 'name' }"
        class="col-md-4 col-sm-6"
        label="Node 1:"
        required
      />
      <VSelect
        id="node-v"
        v-model="linkInEdit.node_v.value"
        :parent-error="linkInEdit.node_v.error"
        :options="nodes"
        :keys="{ value: 'name', text: 'name' }"
        class="col-md-4 col-sm-6"
        label="Node 2:"
        required
      />
      <div class="col-md-3 col-sm-4">
        <button class="btn btn-success" @click.prevent="addLink">
          {{ editingNodeOrLink ? "save link" : "add link" }}
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import VInput from "./VInput";
import VSelect from "./VSelect";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "FormInputLink",
  components: { VInput, VSelect },
  computed: mapGetters([
    "nodes",
    "linkInEdit",
    "editingNodeOrLink",
    "shouldCalculateDistance"
  ]),
  methods: mapActions(["addLink"])
};
</script>
