<template>
  <div class="custom-control custom-checkbox">
    <input
      :id="$attrs.id"
      :value="checked"
      :class="{ 'is-invalid': parentError }"
      v-bind="$attrs"
      type="checkbox"
      class="custom-control-input"
      @change="$emit('change', $event.target.checked)"
    />
    <label :for="$attrs.id" class="custom-control-label">{{ label }}</label>
    <div v-if="parentError" class="invalid-feedback">{{ parentError }}</div>
  </div>
</template>

<script>
export default {
  name: "VCheckbox",
  inheritAttrs: false,
  model: { prop: "checked", event: "change" },
  props: {
    checked: { type: Boolean, default: true },
    label: { type: String, default: "" },
    parentError: { type: String, default: "" }
  }
};
</script>

